<template>
  <div class="reg">
    <div class="content">
      <div class="logo">
        <h1>欢迎登录</h1>
      </div>
      <div class="content_a">
        <van-divider
            :style="{ color: '#000', borderColor: '#4c4f50', padding: '0 16px'}"
        >
          <router-link to=""><span>登录</span></router-link>
          <i></i>
          <router-link to="/reg"><span>注册</span></router-link>
        </van-divider>
      </div>
      <div class="content_input">
        <van-form @submit="onSubmit">
          <van-field
              v-model="username"
              name="用户名"
              label="用户名"
              placeholder="请输入用户名"
              :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
              v-model="password"
              type="password"
              name="密码"
              label="密码"
              placeholder="请输入密码"
              :rules="[{ required: true, message: '请填写密码' }]"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit" color="#f55657">登录</van-button>
            <van-checkbox icon-size="14px" style="color:#868686;padding-left:0.15rem">已阅读并同意"用户协议"和"隐私政策" </van-checkbox>
          </div>
          <p style="color:#000;position:fixed;bottom:0.05rem;left:50%;transform: translateX(-50%)">遇到问题</p>
        </van-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "reg",
  data() {
    return {
      username: "",
      password: "",
      uploader: [{url: 'https://img01.yzcdn.cn/vant/leaf.jpg'}],
      value: ""
    }
  },
  beforeCreate() {
    this.$store.commit('load', false)
  },
  methods: {
    onSubmit() {
      this.$axios({
        url: "/login",
        method: 'post',
        data: {
          username: this.username,
          password: this.password
        }
      }).then(res => {
        if (res.data.err)
          alert(res.data.title)
        else {
          alert(res.data.title)
          const d = new Date();
          d.setDate(d.getDate() + 1);
          document.cookie = `token=${res.data.token};expires=${d}`;
          this.$router.push('/home')
        }
      })

    }
  }
}
</script>

<style scoped lang="scss">

.reg {

  .content {

    .logo {
      h1 {
        font-size: 30px;
        letter-spacing: 5px;
        text-align: center;
        color: #4b4b4b;
        margin: 0.8rem auto 0;
      }
    }

    .content_a {
      margin: 30px 0;

      span {
        font-size: 20px;
      }

      i {
        width: 1px;
        height: 20px;
        background: #000;
        margin: 0 5px;
      }

    }

    .content_input {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

  }
}
</style>